<template>
	<div class="setupbox">
		<div class="setuptitle">
            <span class="setupt"></span>
            <i class="iconfont back" onclick="javascript:history.back(-1);">&#xe624;</i>
        </div> 
		<div class="systemnotice">
			<div class="h2t"></div>
			<div class="newmessage bb">
				<p class="newtit"></p>
				<p class="send"></p>
				<mt-switch class="tapswitch" v-model="value"></mt-switch>
			</div>
		</div>
        <div class="prenotice">
			<div class="newmessage">
				<p class="prenot"></p>
				<p class="neworder"></p>
				<mt-switch class="pretapswitch" v-model="value1"></mt-switch>
			</div>
		</div>
		
		<div class="shortnotice">
			<div class="h2t"></div>
			<div class="newmessage bb">
				<p class="newtit"></p>
				<p class="send"></p>
				<mt-switch class="tapswitch" v-model="value2"></mt-switch>
			</div>
		</div>
		<div class="clearing">
			<span class="cleartit"></span><i class="iconfont personmore">&#xe609;</i>
		</div>
        <p class="exitp"><input @click="exitLogin"  id="exitbtn" type="button" value=""/></p>
        </div>
</template>

<script>
	import {mapState,mapMutations} from 'vuex'
	 export default {
		name:'setup',
		data:function(){
			return{
				value:false,
				value1:false,
				value2:false
			}
		},
		
		methods:{
			...mapMutations(['exitLogin'])
		}

		
      
    }
    
</script>

<style>
</style>


