
import getpos from '../../modules/position.js'
import { Toast } from 'mint-ui';
import router from '../../router'
const  actions = {

    //
    getPosition({ commit }){    
          setTimeout(() => {             
            	getpos((info)=>{
                    commit('getPosi',info)
                 })
         }, 1000);
    },
    getMes({commit},mes){
        $.post("/apiuser/selectHouse",mes,function(data) {
            if(data.result==1){
                //
                console.log("")  
                
            } else {
                console.log("");
                router.push({path:'mysearch'})
            }
        });
        commit('getMes',mes.place)
    },
   
    startDate({commit},start){
        setTimeout(() => {             
            let result ='ok'
            if(result=='ok'){
                commit('startDate',start)
            }
        }, 1000);
    },
    endDate({commit},end){     
        setTimeout(() => {             
            let result ='ok'
            if(result=='ok'){
                commit('endDate',end)
            }
        }, 1000);
    },
    //
    send(){
        // console.log('')
        axios('/dola/app/user/newgetverifycode').then(({data})=>{
            if(data.code===2001){
                console.log('1234')
            }
        })
    },
    //
    login({commit},user_info){
        console.log(user_info)
        // axios('/dola/app/user/newgetverifycode').then(({data})=>{
        //     if(data.code===2001){
        //         console.log('1234')
        //         Toast.success('1234',1,()=>{			                   
        //         },true)
        //     }else{
        //         Toast.fail('')
        //     }            
        // })
        setTimeout(() => {             
           if(user_info.name=='123'&&user_info.pass=='456'){
            //    alert('')
            router.push({path:'/mine'});
            commit('login',user_info)	    		                   
            Toast({
                message: '',
                iconClass: 'icon icon-success'
              });
               
            }else{
  
            // alert('')
             Toast('');
               
            }         
           
        }, 1000);

    },
    //
    submitSuggest(){
        router.push({path:'/mine'});    		                   
        Toast({
            message: '',
            iconClass: 'icon icon-success'
          });
        console.log('132')
    },
    //
    subOrder(){
        Toast('');
        router.push({path:'/mains'});
    }

}

export default actions


